import React, {Component} from "react";
import {connect} from "react-redux";
import {setOfferType,setOfferTypeCmsConf,getDocuments, getParamsFromUrl} from "eshop/modules/simfree/actions/app";
import {selectProductDetailsProcessType} from "eshop/modules/configurator/actions/filters";
import {
    getOfferTypesForSelect,
    getSelectedSoftBundleGroup,
    getSelectedVariantObject,
} from "eshop/modules/simfree/selectors";
import {getSelectedOfferType} from "eshop/modules/configurator/selectors/filters"
import {OraSelect} from "eshop/components/OraCommonComponents";
import OfferType from "../../constants/OfferTypeEnum";
import {getAddTerminalToOfferBundleNo} from "eshop/modules/cart/selectors";
import {
    getPropositionListCount,
    getPropositionListCountSetMode,
    getPropositionListOfferType,
} from "eshop/modules/configurator/selectors/filters";
import { getMarketContext } from "eshop/modules/configurator/selectors/filters"
import StayLoveMsisdnHeader from "eshop/modules/cart/components/entriesList/StayLoveMsisdnHeader";
import {ConditionalInfoComponent} from "../../../../components/InfoComponent";
import {getContractRole} from "../../../configurator/selectors/offers";
import {getStayLoveRetentionMSISDN, hasLove, hasODF} from "../../../auth/selectors/authorization";
import * as PropTypes from "prop-types";
import {isDuet} from "../../selectors";
import SalesChannel from "../../../core/enum/SalesChannel";
import SoftBundleGroup from "../../../core/enum/SoftBundleGroup";
import MarketSegment from "../../../core/enum/MarketSegment";
import BundleType from "../../../cart/components/entriesList/BundleTypeEnum";

class ProductDetailsOfferTypeComponent extends Component {

    constructor(props) {
        super(props);
        const cmsConf=this.props.cmsConf;
        this.props.registerCmsConfiguration(cmsConf);

        if(this.props.cmsConf.length===1 && this.props.selectedOfferType!==cmsConf.offerType){
            this.props.setOfferType(cmsConf[0].offerType);
        }
        if(this.props.selectedOfferType === OfferType.SIMFREE){
            this.props.setOfferType(this.props.selectedOfferType);
        }
        this.selectOfferType = this.selectOfferType.bind(this);
    }

    componentDidMount(){
    }

    selectOfferType(e) {
        this.props.getParamsFromUrl();this.props.setOfferType(e.value)
    }
    renderConditionalInfo = ({channels, selectedOfferType, softBundleGroup, hasLove}) => {
        if (channels.sales === SalesChannel.WWW &&
            selectedOfferType === OfferType.VOICE_LDF &&
            softBundleGroup === SoftBundleGroup.LDF) {
            return (
                    <ConditionalInfoComponent
                        condition={hasLove}
                        type={"blueInfo"}
                        color={"black"}
                        bgColor={"white"}
                        title={"PamiÄ™taj!"}
                        padding={"noPadding"}
                        text={"JesteÅ› w Love dla Firm - dziÄ™ki temu korzystasz ze specjalnych rabatÃ³w. JeÅ›li chcesz korzystaÄ‡ z nich dalej - musisz mieÄ‡ aktywnÄ… umowÄ™ na internet dla biura."}
                        altText={"Love dla firm to pakiet obejmujÄ…cy abonament komÃ³rkowy i internet stacjonarny w Orange dziÄ™ki czemu koszt pierwszej karty jest od 35,00 zÅ‚ + VAT, a drugÄ… kartÄ™ dostaniesz za 0 zÅ‚."}>
                    </ConditionalInfoComponent>
            );
        }
    };

    filterVisible(options){
        let cmsConf=this.props.cmsConf;
        if(!!cmsConf[OfferType.VOICE_LDF] && this.props.stayLoveRetentionMSISDN){
            cmsConf[OfferType.VOICE_LDF].visible=true;
        }
        return options.filter(option=> cmsConf[option.value] && cmsConf[option.value].visible);
    }
    render() {
        let selectedOfferType=this.props.selectedOfferType;
        const selectedOfferTypeDescription=this.props.cmsConf[selectedOfferType]?this.props.cmsConf[selectedOfferType].productListSelectText:OfferType.SIMFREE;
        let options;
        if(selectedOfferType !== OfferType.CONVERGENT){
            options = this.filterOutNotAvailableOfferTypes(this.props.selectValues.filter(o => OfferType.CONVERGENT !== o.value ));
            selectedOfferType = this.doesContainOfferType(this.props.selectedVariantObject.availableOfferTypes,selectedOfferType) ?
                selectedOfferType : this.changeOfferType();
        } else {
            options = this.props.selectValues.filter(t => OfferType.CONVERGENT === t.value );
        }
        options=this.filterVisible(options);
        //For duets - no worlds change during sim count loop
        if ((this.props.propositionListCount > 0 && this.props.isPropositionListCountLoop && !this.props.isB2B) || this.props.isDuet) {
            return null
        }
        return (
            <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-l u-padding-top-l">
                {!this.props.addTerminalToOfferBundleNo &&
                <OraSelect
                    onChange={this.selectOfferType}
                    className="o-select opl-select-light u-inline-block u-small-block u-margin-l u-small-no-margin "
                    id="offerTypeFilter"
                    options={options}
                    withEmptyOption={false}
                    selected={selectedOfferType}
                    selectedName={selectedOfferTypeDescription}
                    selectionType={this.props.selectionType}
                    singleOptionClassName="u-font-bold u-no-padding-left"/>

                }
                {
                    this.renderConditionalInfo(this.props)
                }
                <StayLoveMsisdnHeader/>
            </div>

        )
    }

    filterOutNotAvailableOfferTypes(options){
        return options.filter(o => this.doesContainOfferType(this.props.selectedVariantObject.availableOfferTypes, o.value));
    }

    changeOfferType(){
        let offerType = "";
        let defaultProcess = "";

        this.props.selectedVariantObject.availableOfferTypes.map(o => {
            if(this.props.cmsConf[o]){
                offerType = o;
                defaultProcess = this.props.cmsConf[o].defaultProcess;
                return;
            } else if (o === BundleType.SIMFREE && this.props.cmsConf[OfferType.SIMFREE]){
                offerType = OfferType.SIMFREE;
                defaultProcess = this.props.cmsConf[o].defaultProcess;
                return;
            }
        });

        this.props.setOfferType(offerType);
        if(defaultProcess && defaultProcess !== ""){
            this.props.setProcessType(defaultProcess)
        }
        return offerType;
    }

    doesContainOfferType(availableOfferTypes, offerType){ //necessary only for sog and simfree instalment
        if(offerType !== OfferType.SIMFREE && offerType !== OfferType.SIMFREE_INST){
            return true;
        }

        let result = false;
        availableOfferTypes.map(e => {
            if(e === offerType || (offerType === OfferType.SIMFREE_INST && e === BundleType.SIMFREE)){
                result = true;
                return;
            }
        });

        return result;
    }
}

const mapDispatchToProps = dispatch => ({
    registerCmsConfiguration: cmsConf => dispatch(setOfferTypeCmsConf(cmsConf)),
    getParamsFromUrl: () => dispatch(getParamsFromUrl()),
    setOfferType: offerType => dispatch(setOfferType(offerType)),
    setProcessType: value => dispatch(selectProductDetailsProcessType(value)),
    getDocuments: () => dispatch(getDocuments()),
});

const mapStateToProps = state => ({
    selectValues: getOfferTypesForSelect(state),
    selectedOfferType: getSelectedOfferType(state),
    addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
    selectedVariantObject: getSelectedVariantObject(state),
    isPropositionListCountLoop: getPropositionListOfferType(state) && getPropositionListOfferType(state) === getSelectedOfferType(state),
    propositionListCount: getPropositionListCount(state),
    isB2B: getMarketContext(state) === MarketSegment.B2B,
    softBundleGroup: getSelectedSoftBundleGroup(state),
    hasLove: hasLove(state),
    hasODF: hasODF(state),
    stayLoveRetentionMSISDN: getStayLoveRetentionMSISDN(state),
    isDuet: isDuet(state),
});
ProductDetailsOfferTypeComponent.PropTypes = {
    softBundleGroup: PropTypes.string,
    hasLove: PropTypes.bool,
    hasODF: PropTypes.bool,
};
export default connect(mapStateToProps, mapDispatchToProps)(ProductDetailsOfferTypeComponent);